import React, { useState, useEffect, useCallback } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import MaterialTable from 'material-table';
import Page from '../../../components/Page';
import useIsMountedRef from '../../../hooks/useIsMountedRef';
import useBranch from '../../../hooks/useBranch';
import { Container, Box, IconButton, SvgIcon, makeStyles } from '@material-ui/core';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { Edit as EditIcon, Trash } from 'react-feather';
import branchService from '../../../services/branchService';
import DeleteConfirmCheck from '../../../components/DeleteConfirmCheck';
import { useTranslation } from 'react-i18next';
import Header from './Header';
// import Results from './Results';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3)
  }
}));

export default function BranchList() {
  const { t } = useTranslation();
  const { branches, deleteBranch } = useBranch();

  const classes = useStyles();

  const [open, setOpen] = React.useState(false);
  const [needDeleteBranch, setDeleteBranch] = React.useState({});

  const handleClickOpen = (branch) => {
    console.log(branch);
    setDeleteBranch(branch);
    setOpen(true);
  };

  const handleDeleteBranch = async () => {
    await deleteBranch(needDeleteBranch.id);
    setOpen(false);
    setDeleteBranch({});
  };

  const handleClose = () => {
    setOpen(false);
  };

  const columns = [
    {
      title: t('Name'),
      field: 'name'
    },
    {
      title: t('Code'),
      field: 'code'
    },
    {
      title: t('Store Name'),
      field: 'store_name'
    },
    {
      title: t('Actions'),
      field: 'actions',
      render: (rowData) => (
        <>
          <IconButton component={RouterLink} to={`/branches/${rowData.id}/edit`}>
            <SvgIcon fontSize="small">
              <EditIcon />
            </SvgIcon>
          </IconButton>
          <IconButton onClick={() => handleClickOpen(rowData)}>
            <SvgIcon fontSize="small">
              <Trash />
            </SvgIcon>
          </IconButton>
        </>
      )
    }
  ];

  if (!branches) {
    return null;
  }

  return (
    <Page className={classes.root} title={t('Branch List')}>
      <Container maxWidth={false}>
        <Header />
        {branches && (
          <Box mt={3}>
            <MaterialTable title={t('Branch List')} columns={columns} data={branches} />
          </Box>
        )}
      </Container>
      <DeleteConfirmCheck
        open={open}
        handleClose={handleClose}
        handleDelete={handleDeleteBranch}
        checkText={`${needDeleteBranch.name} (${needDeleteBranch.code})`}
      />
    </Page>
  );
}
